@import url('../../../../style/common.less');

#FAQ {
    padding: 100px 0 45px 0;
    background-color: @theme-bg-color;
    font-family: @Poppins-SemiBold;
    color: #11CEE5;
    position: relative;

    .FAQ-bg {
        position:absolute;
        right: 25px;
        bottom: 20px;
        >img {
            width: 1156px;
            height: 272px;
        }    
    }

    .top {
        display: flex;

        img {
            width: 60px;
            height: 60px;
            margin-right: 30px
        }

        span {
            font-size: 48px;
            line-height: 48px;
            font-weight: 600;
        }

    }

    .FAQ-container {
        margin-top: 45px;
        
        .FAQ-box {
            display: flex;
            font-family: Poppins-SemiBold;
            font-size: 22px;
            color: #11CEE5;
            line-height: 22px;
            font-weight: 600; 
            margin-bottom: 55px;
            cursor: pointer;
           
            .FAQ-col2 {
                flex-grow: 1;
                padding-left: 23px;
                &-Q {
                    
                }
                &-A {
                    padding-top: 30px;
                    font-size: 16px;  
                    color: #FFFFFF;  
                    line-height: 2em;
                    height: 0;
                    overflow: hidden;
                }
                .A-open {
                    height: 100%;
                    p {
                        margin-bottom: 0;
                    }
                    // overflow: none;
                    // animation: open 0.5s ease-in;
                }
                .A-close {
                    height: 0;
                    // overflow: hidden;
                    // animation: close 0.5s ease-in;
                }
            }
            .FAQ-col3 {
                img {
                    cursor: pointer;
                }
            }
        }
    }
}



@keyframes open {
    0% {
        height:  0;
    }
    100% {
        height: 100%;
    }
}

@-webkit-keyframes open {
    0% {
        height:  0;
    }
    100% {
        height: 100%;
    }
}

@keyframes close {
    0% {
        height:  100%;
    }
    100% {
        height: 0%;
    }
}

@-webkit-keyframes close {
    0% {
        height:  100%;
    }
    100% {
        height: 0;
    }
}