@import url('../../../../style/common.less');

.footer {
    padding: 40px 0 35px 0;
    font-size: 12px;
    line-height: 12px;
    font-family: @Poppins-Regular;
    font-weight: 400;
    background-color: #111322;

    &-logo {
        width: 132px;
        height: 23px;
    }

    &-webintro {
        margin: 20px 0;

        span {
            height: 12px;
            color: #FFFFFF;
            font-weight: 400;
        }
    }

    &-copyright {
        margin-top: 40px;
        height: 12px;
        color: #11CEE5;
        line-height: 12px;
    }

    &-link {
        display: flex;

        .link-box {
            width: 40px;
            height: 40px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-right: 10px;
            border: 1px solid rgba(17,206,229,1);
            cursor: pointer;

            img {
                width: 20px;
                height: 20px;
            }

            &:hover {
                border: 3px solid rgba(17,206,229,1);
                transition: 0.5s;
            }
        }
    }
}