@import url('../../../../style/common.less');

#home {
    width: 100%;
    // min-width: 1200px;
    // height: 1000px;
    position: relative;
    background-color: @theme-bg-blue-black;
    padding: 167px 0 153px 0 ;
    
    .intro-bg {
        position: absolute;
        top: -80px;
        left: -180px;
        width: 1100px;
    }


    .intro-card-container {
        display: flex;
        justify-content: flex-end;
        
        .intro-card {
            width: 656px;
            position: relative;

            &-title {
                padding-top: 24px;
                position: relative;

                .title-font {
                    display: block;
                    width: 400px;
                    height: 94px;
                    // background-image: url(../../../../assets/images/logo.png);
                    // background: url('src/assets/images/logo.png') no-repeat center/100%;
                }

                .title-font-bg {
                    position: absolute;
                    display: block;
                    height: 104px;
                    width: 100%;
                    top: 56px;
                }
                
            }

            &-content {
                margin: 30px 0;
                font-family: @Poppins-Regular;
                font-size: 20px;
                color: #FFFFFF;
            }

            &-link {
                width: 380px;
                height: 140px;
                display: flex;
                justify-content: space-between;
                align-content: space-between; 
                flex-wrap: wrap;

                .website-card {
                    display: flex;
                    align-items: center;
                    width:180px ;
                    height: 60px;
                    padding: 15px 0;

                    font-family: @Poppins-SemiBold;
                    font-size: 16px;
                    font-weight: 600;
                    color: #010210;
                    text-align: center;
                    background-color: #11CEE5;
                    cursor: pointer;
                    
                    .card-left {
                        flex-grow: 2;
                        border-right: 1px solid #010210;
                    }
                    .card-right {
                        flex-grow: 1;
                        img {
                            width: 16px;
                            height: 16px;
                        }
                    }
                }
            }
        }
    }
}