@import url('../../../../style/common.less');

#tiandi {
    background-color: #000;
    padding: 100px 0;
    text-align: center;

    .intro-title {
        margin-bottom: 40px;
        font-family: @Poppins-SemiBold;
        font-size: 48px;
        color: #11CEE5;
        height: 48px;
        line-height: 48px;
        font-weight: 600;
    }

    .intro-content {
        width: 1000px;
        margin: 0 auto;
        font-family: @Poppins-Regular;
        font-size: 20px;
        p {
            margin-bottom: 0.5em;
        }
    }
    
    .bank-main {

        .bank-divider {
            width: 606px;
            height: 50px;
            margin: 18px 0 30px 0;
        }

        .bank-products {
            display: flex;
            justify-content: space-between;
            align-content: space-between;
            flex-wrap: wrap;
            height: 700px;

            .prod-card {
                // width: 278px;

                .prod-img {
                    width: 278px;
                    height: 278px;
                }
                .prod-footer {
                    display: flex;
                    
                    .prod-left {
                        flex-grow: 1;
                        font-size: 16px;
                        line-height: 16px;

                        span {
                            display: block;
                            text-align: left;
                        }
                        .prod-name {
                            height: 16px;
                            font-family: @Poppins-SemiBold;
                            color: #11CEE5;
                            font-weight: 600;
                            margin: 10px 0;
                        }

                        .prod-id {
                            height: 16px;
                            font-family: @Poppins-Regular;
                            color: #FFFFFF;
                        }
                    }

                    .prod-right {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        flex-grow: 0;

                        .prod-icon {
                            width: 30px;
                            height: 30px;
                            flex-grow: 0;
                            background-color: #11CEE5;
                            cursor: pointer;

                            img {
                                width: 16px;
                                height: 16px;
                            }
                        }
                    }
                    
                }
            }
        }
    }
}