@import url('../../../../style/common.less');

.header {
    position: fixed;
    top: 0;
    height: 80px;
    width: 100%;
    margin: 0 auto;
    background-color: @theme-bg-blue-black;
    z-index: 1;
    

    .top-nav {
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        img {
            width: 160px;
            height: 38px;
            cursor: pointer;
        }

        &-right {
            display: flex;
            align-items: center;
            color: #ffffff;
            font-family: @Poppins-Regular;

            .top-nav-anch {
                margin-right: 120px;
                display: flex;
                justify-content: space-between;
                align-items: center;

                a {
                    height: 20px;
                    font-size: 16px;
                    color: #ffffff;
                    text-align: center;
                    line-height: 16px;
                    margin-right: 40px;
                    cursor: pointer;
                    // transition: 0.5s;
                }

                .anch-active {
                    color: #11CEE5;
                    border-bottom: 1px solid #11CEE5;
                    transition: 0.5s;
                }
            }
            .top-nav-lang {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 110px;
                height: 80px;
                cursor: pointer;
                span {
                    height: 16px;
                    font-size: 16px;
                    line-height: 16px;
                    margin-right: 6px;
                }
                img {
                    width: 10px;
                    height: 8px;
                }
            }
        }
    }
}

.nav-lang {
    position: absolute;
    top: 60px;
    right: 0;
    width: 110px;
    padding: 20px 15px;
    opacity: 0.9;
    background: #FFFFFF;
    border: 1px solid rgba(246,250,254,1);
    box-shadow: 0px 5px 5px 0px rgba(0,0,0,0.16);
    border-radius: 8px;
    
    line-height: 12px;
    font-family: PingFangSC-Semibold;
    font-size: 12px;
    text-align: center;
    font-weight: 600;

    transition: 0.5s;

    &-item {
        width: 80px;
        height: 30px;
        border-radius: 15px;
        padding: 9px 0;
        color: #8696A0;
        cursor: pointer;
    }

    &-item:nth-child(n+2) {
       
        margin-top: 10px;
    }

    &-item-active {
        background: #11CEE5;
        color: #000000;
    }
}