@import url('../../../../style/common.less');

#roadmap {
    position: relative;
    height: 761px;
    width: 100%;
    padding: 100px 0;
    background-color: @theme-bg-blue-black;
    font-family: @Poppins-SemiBold;
    color: #11CEE5;
    
    .map-bg {
        position: absolute;
        right: 0;
        bottom: 0;
        img {
            width: 815px;
            height: 661px;
        }
    }

    .road-container {
        .road-title {
            display: flex;
            align-items: center;
            margin-bottom: 50px;
            img {
                width: 60px;
                height: 60px;
                margin-right: 30px;
            }
            span {
                height: 48px;
                font-size: 48px;
                line-height: 48px;
                font-weight: 600;
            }
        }

        .road-footerprint {
            display: flex;
            min-height: 104px;
            font-size: 22px;

            .footer-col1 {
                line-height: 34px;
                height: 34px;
            } 

            .footer-col2 {
                padding: 0 24px;
                .col2-img-container {
                    height: 34px;
                    width: 34px;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    img {
                        transform: rotate(-90deg);
                    }
                }
                

                &::after {
                    content: '';
                    display: block;
                    height: 60%;
                    margin: 6px auto;
                    width: 1px;
                    background-color: #11CEE5;
                }
            }

            .footer-col3 {
                .desc-line {
                    display: flex;
                    // line-height: 22px;
                    // margin-bottom: 30px;
                    .desc-line-img {
                        height: 34px;
                        display: flex;
                        align-items: center;
                        img {
                            width: 22px;
                            height: 16px;
                            flex-shrink: 0;
                        }
                    }
                    
                    .footer-desc {
                        padding-left: 10px;
                        color: #11CEE5;
                        // p {
                        //     color: #11CEE5;
                        // }
                    }
                }
                
            }

        }
        .road-footerprint:last-child {
            .footer-col2::after {
                display: none;
            }
        }
    }
}