@import url('../../../../style/common.less');

#team {
    background-color: @theme-bg-color;
    width: 850px;
    padding: 100px 0;
    margin: 0 auto;
    
    .team-container {
        margin: 0 auto;

        .team-divider {
            display: flex;
            justify-content: center;
            align-items: center;

            .divider-left,
            .divider-right {
                height: 14px;
                width: 268px;
                align-self: flex-end;
                margin-bottom: 12px;
            }

            .divider-center {
                margin: 0 30px;
                display: flex;
                justify-content: center;
                align-items: center;

                img {
                    width: 60px;
                    height: 60px;
                    margin-right: 30px;
                }

                span {
                    display: block;
                    height: 48px;
                    font-family: @Poppins-SemiBold;
                    font-size: 48px;
                    color: #11CEE5;
                    line-height: 48px;
                    font-weight: 600;
                }
            }


        }
        
        .team-avatar {
            margin-top: 50px;
            display: flex;
            justify-content: space-between;
            
            &-box {
                position: relative;

                img {
                    display: block;
                    width: 360px;
                    height: 360px;
                }

                .team-avatar-desc {
                    display: flex;
                    justify-content: center;
                    position: absolute;
                    right: -40px;
                    bottom: 0;
                    width: 327px;
                    height: 100px;
                    padding: 30px 0;
                    opacity: 0.9;
                    background: #11CEE5;
                    border-radius: 30px 0px 0px 0px;

                    .desc-left {
                        width: 30px;
                        height: 30px;
                    }

                    .desc-right {
                        padding-left: 17px;
                        
                        span {
                            display: block;
                        }
                        
                        .desc-title {
                            height: 28px;
                            font-family: @Poppins-SemiBold;
                            font-size: 28px;
                            color: #000000;
                            line-height: 28px;
                            font-weight: 600;
                        }

                        .desc-extra {
                            height: 20px;
                            font-family: PingFangSC-Regular;
                            font-size: 20px;
                            color: #333333;
                            line-height: 20px;
                            font-weight: 400;
                            margin-top: 10px;
                        }
                    }
                }
            }
            

            
        }
    }

}